/* Custom CSS */
body{
  background: #EAE8FF;  /* fallback for old browsers */
   /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  margin: 0;
  overflow-x: hidden;
} 
.header{
  width: 100%;
  background-color: #2d3142;
  border-radius: 0px 0px 0px 0px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
}
.header .logo{
  padding: 10px 10px;
}
.sidebar{
  height: 100%;
  padding: 30px;
  background-color: #f4f3fc;
}
.main-grid{
  background-color: #f4f3fc;
  padding: 30px;


}
.close{
  margin-left: 10px;
  color: #fff !important;
  text-shadow: none !important;
  opacity: 1 !important;
  border-radius: 100%;
}
.title{
  margin-top: 50px;
}
.majorDropdown{
  font-size: 24px;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
}
.majorDropdown:focus{
  outline: none;
}
.search-btn{
  display: inline-block;
  background-color: #fff;
  border-radius: 100%;
  margin-left: 10px;
  border: 0px;
  padding: 5px 10px 5px 10px;
}
.search-btn:focus{
  outline: none;
  background-color: #1f0664;
  color: #fff;
}
.search-bar{
  font-size: 18px;
  padding: 5px 10px 5px 10px;
  margin: 10px 0px 10px 0px;
  border-radius: 15px;
  border: 0px;
  width: 70%;
  display: inline-block;
}
.search-bar:focus{
  outline: none;
}
.majorDropdown:focus{
  outline: none;
}
.core-list, .elective-list, .breadth-list, .selected-list{
  list-style: none;
  padding-left: 0%;
  display: inline-block;
}
.core-item, .elective-item, .breadth-item, .selected-item{
  color: #fff;
  background-color: #1f0664;
  border-radius: 10px;
  display: inline-block;
  padding: 10px 10px 10px 10px;
  margin: 5px 5px 5px 0px;
}